.event-card {
    min-width: 900px;
    width: 100%;
    height: 350px;
    background-color: #F1EFEF;
    color: #222831;
    border-radius: 20px;
    padding: 34px 33px 34px 39px;
    cursor: pointer;
    transition: 0.3s;
    position: relative;
    filter: blur(5px);
  -webkit-filter: blur(5px);
}
.event-card:not(:last-child){
    margin-right: 30px;
}
.date {
    font-weight: 300;
    font-size: 50px;
    line-height: 57px;
    text-transform: uppercase;
}

.date p:nth-child(2) {
    font-size: 25px;
    margin-top: -20px;
    margin-bottom: 80px;
}

.event-dis {
    font-weight: 400;
    font-size: 25px;
    line-height: 18px;
    text-transform: capitalize;
}

.event-dis p:nth-child(1){
    margin-bottom: 20px;
}
.event-dis p:nth-child(2),
.event-dis p:nth-child(3) {
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    text-transform: capitalize;
    color: #36C5F0;
}

.event-card:hover .event-dis p:nth-child(2){
    color: #FFFFFF;
}
.event-card:hover .event-dis p:nth-child(3) {
    color: #FFFFFF;
}
.event-card:hover{
    background-color: #36C5F0;
}