.hero {
  background-color: #f7f7f7;
  border-radius: 0px 0px 330px 0px;
}
.hero-inner {
  padding: 169px 0 50px 0;
  display: flex;
}
.hero-inner span {
  line-height: 21px;
  text-transform: capitalize;
}
.hero-inner h1 {
  font-weight: 700;
  line-height: 46px;
  text-transform: capitalize;
  background: linear-gradient(91.33deg, #b31848 22.53%, #36c5f0 86.64%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
  margin-bottom: 10px;
}
.hero-s-bottom {
  font-weight: 400;
  font-size: 30px;
  line-height: 34px;
  text-transform: capitalize;
}
.hero-left{
  margin-top: 20px;
}
.hero-right {
  width: 500px;
  height: 500px;
}
.hero-right img {
  box-shadow: 4px 4px 19px 0px grey;
  object-fit: qfill;  
  margin-top: -100px;
  margin-left: 0px;
  transition: 0.3s;
}
.btn-group{
  margin-top: 70px;
}
.btn-group a{
    display: inline-block;
    padding: 15px 46px;
    line-height: 23px;
    text-transform: capitalize;
    cursor: pointer;
}
.btn-group a:nth-child(1){
    background-color: #B31848;
    transition: 0.3s;
}
.btn-group a:nth-child(2){
    background-color: #36C5F0;
    margin-left: 30px;
    transition: 0.3s;
}
.btn-group a:nth-child(1):hover{
  background-color: #910e37;
}
.btn-group a:nth-child(2):hover{
  background-color: #1796bd;
}