.footer{
    background-color: #0D1C2B;
    color: #f1f1f1
}
address{
    font-style: normal;
    margin-bottom: 15px;
}
a{
    text-decoration: none;
    color: #ffffff;
}
li{
    font-weight: 400;
    font-size: 18px;
    line-height: 34px;
}
h2{
    margin-bottom: 10px;
}
.footer-inner{
    padding-top: 111px;
    padding-bottom: 100px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr;
    grid-row-gap: px;
    grid-column-gap: 30px;
}
.data-text{
    height: 209px;
    width: 302px;
}
.data-text h2{
    font-family: 'Press Start 2P', cursive;
    font-style: normal;
    font-weight: 400;
    font-size: 25px;
    line-height: 25px;
}
.data-text p{
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
}
.sub{
    margin-top: 30px;
    
}
.sub h2{
    font-size: 22px;
}
.sub input{
    border: none;
    font-size: 16px;
    border-radius: 10px;
    width: 100%;
    padding: 15px 20px;
}
.sub button{
    padding: 15px 46px;
    font-size: 20px;
    line-height: 23px;
    text-transform: capitalize;
    color: #FFFFFF;
    border-radius: 10px;
    background-color: #B31848;
    transition: 0.3s;
    margin-top: 15px;
    cursor: pointer;
    border: none;
}
.sub button:hover{
    background-color: #880f35;
}
.programs-up{
    Width:213px;
    Height:250px;
}
.sub button:focus,
.sub input:focus{
    outline: none;
}

.programs-down address{
    font-weight: 400;
    font-size: 18px;
    line-height: 34px;
    margin-top: 10px;
}
.Corporate-Services{
    width: 217.99996948242188px;

}
.Corporate-Services h2{
    font-weight: 700;
    font-size: 25px;
    line-height: 29px;
}

.corporate-down h6{
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
}
.company-up{
    height: 286px;
    width: 160px;
}
.resources{
    margin-top: 40px;
    margin-bottom: 10px;
}
.contac{
    font-family: 'Ubuntu';
    font-style: normal;
    font-weight: 700;
    font-size: 25px;
    line-height: 29px;
    height: 29px;
    width: 174px;
    border-radius: nullpx;

}
.social-icon{
    height: 23px;
}
.social-icon a:not(:last-child){
    padding-right: 12px;
}
.social-icon a img{
    transition: 0.2s;
}
.social-icon a:hover img{
    position: relative;
    top: -1px;
    transform: scale(1.1);
}

.cell{
    margin-top: 53px;
    height: 21px;
    width: 139px;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
}
li a{
    transition: 0.5s;
}
li a:hover{
    text-decoration: underline;
}