.compani h2{
    line-height: 40px;
    font-weight: 600;
    margin-bottom: 44px;
}
.comCarousel{
    max-width: 900px;
    margin: 0 auto;
    cursor: grab;
    overflow: hidden;
    position: relative;
}
.comCarousel::after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    background: linear-gradient(90deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%);
    width: 100px;
}
.comCarousel::before{
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%);
    z-index: 2;
    width: 100px;
} 
.item{
    margin: 0 17px;
}
.item img{
    object-fit: contain;
    max-width: 200px;
    max-height: 100px;
    width: auto;
    pointer-events: none;
}
.comCarosule-inner{
    display: flex;
    justify-content: center;
}