.career {
    position: relative;
}
.bg-group{
    z-index: -2;
    position: absolute;
    min-width: 100%;
}
.bg-group::after{
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.69);
}
.bg1{
    min-width: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-image: url(../assets/Images/bg.jpg);
    animation: imageAnimation 70s cubic-bezier(0, 1.08, 0, 1.03) 3s infinite;
    transition: 0.5s;
}

@keyframes imageAnimation {
    0%{
        background-image: url(../assets/Images/bg.jpg);
    }
    25%{
        background-image: url(../assets/Images/bg2.jpg);
    }
    50%{
        background-image: url(../assets/Images/bg3.jpg);
    }
    75%{
        background-image: url(../assets/Images/bg1.jpg);
    }
    100%{
        background-image: url(../assets/Images/bg.jpg);
    }
}

.career-inner {
    z-index: 44;
    padding: 107px 0 0 0;
}
.career-left {
    max-width: 400px;
    width: 100%;
    margin-right: 20px;
}

.career-right {
    margin-top: 100px;
    height: fit-content;
    max-width: 810px;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 30px;
    grid-row-gap: 30px;
}

.career-red-txt {
    font-weight: 700;
    line-height: 21px;
    color: #B31848;
    margin-bottom: 10px;
}

.career-inner h2 {
    font-weight: 700;
    margin-bottom: 45px;
}

.career-dis {
    font-weight: 400;
    font-size: 14px;
    line-height: 25px;
    margin-bottom: 92px;
}

.career-link {
    font-weight: 700;
    line-height: 28px;
    color: #36C5F0;
    margin-bottom: 85px;
    display: inline-block;
    position: relative;
    transition: 0.2s;
}
.career-link:hover{
    text-decoration: underline;
    color: #75d1ec;
}
.career-link:hover.career-link::after{
    right: -16px;
}
.career-link::after{
    content: url(../assets/svg/rightArrow.svg);
    position: absolute;
    right: -14px;
    top: 0;
    transition: 0.2s;
}

.career-guide {
    position: relative;
    top: 42px;
    padding: 14px 106px 14px 36px;
    justify-content: space-between;
    align-items: center;
    background: linear-gradient(89.82deg, #36C5F0 0.05%, #B31848 99.07%);
}

.career-guide a button{
    border: 3px solid #F7F7F7;
    border-radius: 10px;
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    text-align: center;
    text-transform: capitalize;
    color: #FFFFFF;
    background-color: rgba(0, 0, 0, 0);
    display: inline-block;
    padding: 16px 47px;
    cursor: pointer;
}

.career-guide h3 {
    font-weight: 700;
    font-size: 22px;
    line-height: 25px;
}