
@media only screen and (max-width:1157px) {
    .hero-right img {
        margin-left: 0px;
        position: relative;
        left: -30px;
    }
    .footer-inner {
        grid-column-gap: 15px;
    }
    .Company{
        display: none;
        
    }
    .footer-inner h2{
        font-size: 20px;
    }
    .hero-left{
        margin-top: 0px;
    }
}












@media only screen and (max-width:1000px) {
    .card {
        height: 250px;
        width: 200px;
    }
    .event-card {
        min-width: 600px;
    }

}














@media only screen and (max-width:820px) {
    .mob{
        display: block;
    }
    .career-right{
        grid-template-columns: repeat(1, 1fr);
    }
    .card {
        height: 200px;
        width: 250px;
    }
    .career-right {
        max-width: 260px;
    }
    .career-guide {
        top: 36px;
        padding: 14px 36px;
    }
    .career-guide h3 {
        font-size: 18px;
    }
    .career-guide a button {
        border: 2px solid #F7F7F7;
        padding: 12px 47px;
    }
    .programs-up .mob h6{
        font-size: 16px;
        font-weight: 400;
    }
    .programs-up .mob h2{
        margin-top: 20px;
    }
    .corporate-down{
        display: none;
    }
    .footer-inner{
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 30px;
        justify-items: center;
    }
    .hero-inner{
        padding: 50px 0 87px 0;
        flex-direction: column;
    }
    .hero-left{
        text-align: center;

    }
    .hero-left img{
        width: 100%;
        height: 400px;
        object-fit: cover;
        margin: 30px 0;
    }
    .hero-right{
        display: none;
    }
    .cardMessage h2{
        font-size: 20px;
    }
}

@media only screen and (max-width:500px) {
    .header-inner{
        height: 70px;
    }
    .header-inner img{
        transform: scale(0.9);
    }
    .hero-left img{
        height: 300px;
    }
    .hero-left span{
        font-size: 20px;
    }
    .hero-left h1{
        font-size: 30px;
        letter-spacing: -1px;
        line-height: 30px;
    }
    .btn-group{
        margin-top: 0;
    }
    .btn-group a{
        padding: 10px 25px;
        font-size: 16px;
    }
    .btn-group a:nth-child(2){
        margin-left: 10px;
    }
    .counter-inner{
        padding: 50px 0 80px 0;
    }

    .item img{
        max-width: 100px;
    }
    .item:nth-child(1) img{
        height: 50px;
    }
    .career-inner .df{
        flex-direction: column;
    }
    .career-left{}
    .career-right{
        max-width: 500px;
        width: 100%;
        margin-top: 0;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
    .career-right .card h3{
        font-size: 18px;
        line-height: 16px;

    }
    .career-right .card img{
        width: 100%;
        object-fit: fill;
    }
    .career-right .card{
        width: 150px;
        height: 100px;
    }
    .career-left a{
        margin-bottom: 45px;
    }
    .career-left p{
        margin-bottom: 30px;
    }
    .career-left h2{
        font-size: 35px;
        margin-bottom: 30px;
    }
    .career-guide h3{
        margin-bottom: 16px;
        line-height: 20px;
    }
    .career-guide {
        top: 63px;
        text-align: center;
    }
    .event-dis p:nth-child(1){
        line-height: 20px;
        font-size: 22px;
    }
    .event-card{
        min-width: 90vw;
    }
    .footer-inner{
        grid-template-columns: repeat(1, 1fr);
        grid-column-gap: 30px;
        justify-items: center;
    }
    .data-text{
        height: 190px;
        width: 100%;
    }
    .sub{
        margin-top: 0;
    }
    .Programs{
        height: 100%;
        width: 100%;
        margin-top: 50px;
    }
}