.card {
    height: 300px;
    width: 350px;
    background-color: #fff;
    padding: 30px 51px;
    flex-direction: column;
    justify-content: center;
    position: relative;
    overflow: hidden;
}
.cardMessage{
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.9);
    opacity: 0;
    transition: opacity 0.6s;
}
.cardMessage h2{
    justify-content: center;
}
.card:hover .cardMessage{
    opacity: 1;
}
.card img {
    max-width: 150px;
}

.card h3 {
    margin-top: 12px;
    font-weight: 700;
    font-size: 22px;
    line-height: 25px;
}