/* ==============================
          Global css
============================== */
@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
a{
  text-decoration: none;
}
li{
  list-style: none;
}
body {
  font-family: 'Ubuntu', sans-serif;
}
/* =======================
  Scroll Bar
======================== */
::-webkit-scrollbar {
  width: 15px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 5px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(71, 71, 255); 
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #0e6fba; 
}
/* ==============================
            Common css
============================== */
/* =================flex */
.flex{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.df{
  display: flex;
}
/* ==========font size */
.f-18{
  font-size: 18px;
}
.f-45{
  font-size: 45px;
}
.f-20{
  font-size: 20px;
}
.f-25{
  font-size: 25px;
}
.f-30{
  font-size: 30px;
}
.f-35{
  font-size: 35px;
}

/* ===========color */
.text-color{
  color: #222831;
}
.f-white{
  color: #ffffff;
}

/* ============text align */
.tac{
  text-align: center;
}

/* ============text transfrom */
.ttu{
  text-transform: uppercase;
}
.ttc{
  text-transform: capitalize;
}
/* ============Hight and width 100% */
.hw100{
  height: 100%;
  width: 100%;
}
.w100{
  width:100%
}
.h100{
  height: 100%;
}
/* ============border */
.border-radius-25{
  border-radius: 25px;
}
/* ============Cursor */
.cursor-p{
  cursor: pointer;
}
/* content wrap */
.container{
  max-width: 1210px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}
/* ==============================
============================== */
.mob{
  display:none;
}
